.card-easy-7 {
  $origin: &;
  width: 100%;
  max-width: 300px;

  &__number {
  }

  &__container {
    padding-bottom: 20px;
    border-bottom: 1px solid var(--primary-30p);
  }

  &__image {
  }

  &__title {
    font-family: var(--font-family);
    font-weight: 400;

    @include simple-property-DF(font-size, 28, 26, 24, 22, 18);
    line-height: 130%;
    text-transform: uppercase;
    color: var(--text-talee-title);
  }

  &__text {
    font-family: var(--second-family);
    font-weight: 400;

    @include simple-property-DF(font-size, 18, 18, 16, 16, 14);
    line-height: 130%;
    color: var(--dark-gray);

    & > b {
      font-weight: 500;

      @include simple-property-DF(font-size, 24, 22, 20, 18, 16);
      color: var(--primary);
    }
  }

  &__block {
  }
}
