.block-decor-information {
  margin-bottom: -45%;

  &__main-photo {
    @include size-important(100%);

    aspect-ratio: 1;
    margin: auto;
  }

  &__wrapper {
    position: relative;
    width: 100vw;
    max-width: 1920px;
  }

  &__card-1 {
    position: absolute;
    left: 30px;
    top: -200px;
    width: 50%;

    & .card-easy-6 {
      justify-content: start;

      &__container {
      }

      &__number {
        left: inherit;
        right: 0;

        @include simple-property-DF(padding-right, 170, 70);
      }
    }
  }

  &__card-2 {
    position: absolute;
    right: 30px;
    width: 50%;
    top: 20px;

    & .card-easy-6 {
      padding-left: 200px;

      &__container {
        width: 400px;
      }

      &__number {
        left: inherit;
        right: 0;
      }
    }
  }

  &__card-3 {
    position: absolute;
    top: 10%;
    left: 22%;
    z-index: 1;
    width: 554px;
    height: 330px;

    & article {
      align-items: start;
    }
  }

  &__image {
    @include no-event;
    @include simple-property-DF(height, 1018, 1018 * 0.75, (1018 * 0.75) * 0.75);

    position: relative;
    top: -220px;
    margin: 0 auto;
    z-index: 1;

    overflow: hidden;

    &-wrapper::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      pointer-events: none;
      z-index: 2;

      background: linear-gradient(
        transparent 20%,
        var(--bg) 75%,
        var(--bg) 90%,
        transparent 100%
      );
      // border: 1px solid red;
      
      @include toDesktop {
        background: linear-gradient(
          transparent 25%,
          var(--bg) 68%,
          var(--bg) 90%,
          transparent 100%
        );
      }
      
      @include toLaptop {
        background: linear-gradient(
          transparent 28%,
          var(--bg) 60%,
          var(--bg) 90%,
          transparent 100%
        );
      }
      
      @include toTablet {
        background: linear-gradient(
          transparent 45%,
          var(--bg) 75%,
          var(--bg) 90%,
          transparent 100%
        );
      }
      
      @include toPhone {
        background: linear-gradient(
          transparent 0%,
          var(--bg) 70%,
          var(--bg) 90%,
          transparent 100%
        );
      }
    }
  }

  @include toDesktop {
    &__card-1 {
      top: -85px;
      width: 44%;
    }

    &__card-2 {
      width: 45%;
    }

    &__card-3 {
      top: 13%;
      left: 13%;
    }
  }

  @include toLaptop {
    &__card-1 {
      top: -220px;
      left: 45%;
      right: initial;
      width: 45%;
    }

    &__card-2 {
      top: -66px;
      width: 45%;
      right: initial;
      left: 45%;

      & .card-easy-6 {
        padding-left: 22px;

        &__container {
          width: 100%;
        }

        &__number {
          left: inherit;
          right: 0;

          @include simple-property-DF(padding-right, 170, 70);
        }
      }
    }

    &__card-3 {
      top: 16%;
      left: 45%;
      right: initial;
      width: 45%;
      height: 300px;

      & > .card-block-4 {
        align-items: center;
        padding: 14px 20px;
      }
    }

    &__image {
      margin: 0;
    }
  }

  @include toTablet {
    margin-top: clm_px4(0, 110, $tab, $phone, true);
    margin-bottom: -35%;

    &__card-1,
    &__card-2,
    &__card-3 {
      left: initial;
      right: 2%;
    }

    &__card-3 {
      height: max-content;
      top: 20%;
    }

    &__image {
      top: -100px;

      // &-wrapper::before {
      //   content: "";
      //   position: absolute;
      //   top: initial;
      //   bottom: 0;
      //   left: 0;
      //   width: 100%;
      //   height: 50%;
      //   background: linear-gradient(
      //     transparent 20%,
      //     var(--bg) 50%,
      //     var(--bg) 90%,
      //     transparent 100%
      //   );
      //   pointer-events: none;
      //   z-index: 2;
      // }
    }
  }

  @include toPhone {
    margin-top: clm_px4(0, 120, $phone, $minWindowWidth, true);

    &__card-1,
    &__card-2,
    &__card-3 {
      display: none;
    }

    &__image {
      margin: 0 auto;
    }
  }
}

.section-more-about-company {
  padding-bottom: 60px;

  &__cards {
    display: flex;
    position: relative;
    justify-content: space-around;
    gap: 30px;
    z-index: 2;
    transform: translateX(20px);
    padding-inline: 20px;
  }

  @include toDesktop {
    &__cards {
      transform: translateX(20px);
    }
  }

  @include toLaptop {
    padding-bottom: 0;

    &__cards {
      transform: translateX(0);
    }
  }

  @include toTablet {
    &__cards {
      flex-wrap: wrap;

      & > *:last-child {
        order: -1;
      }
    }
  }

  // @include toPhone {
  //  &__cards {
  //   transform: translateX(20px);
  //  }
  // }
}
